@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
@import url(https://fonts.googleapis.com/css?family=Alex+Brush);
body, html {
  height: 100%;
  width: 100%; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none; }

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px; }

button {
  background: none;
  cursor: pointer;
  color: #ffffff;
  -webkit-transition: all .25s ease-in;
  transition: all .25s ease-in; }
  button:hover, button:active {
    color: #F2C94C; }

input {
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 50px;
  -webkit-transition: all .25s ease-in;
  transition: all .25s ease-in;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.25); }
  input:hover, input:focus {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
    background: rgba(0, 0, 0, 0.75); }

article {
  width: 475px;
  margin: 15px auto;
  padding: 20px;
  border-radius: 5px;
  color: #ffffff;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between; }
  article p {
    margin-bottom: 15px; }
  article sub {
    color: #A7A7A7; }

.App {
  min-height: 100vh;
  background-color: linear-gradient(119.36deg, rgba(23, 117, 63, 0.8) 0%, rgba(6, 175, 76, 0.8) 100%);
  background-size: cover;
  background-position: center center;
  height: 100%;
  width: 100%; }
  .App__title {
    font-family: "Alex Brush", cursive;
    font-size: 75px;
    text-shadow: 0 4px 4px #000000;
    color: #F2C94C;
    display: block;
    text-align: center;
    text-decoration: none; }

.MainForm {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }
  .MainForm form {
    position: relative; }
  .MainForm__input {
    padding: 15px 54px 15px 30px;
    font-size: 20px;
    width: 475px;
    color: #ffffff; }
    @media screen and (max-width: 500px) {
      .MainForm__input {
        width: 350px; } }
  .MainForm__label {
    position: absolute;
    top: 50%;
    right: 24px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    height: 24px; }

.Card__text-wrapper {
  width: 75%; }

.Card--Danger {
  background: rgba(255, 0, 0, 0.25); }
  @media screen and (max-width: 550px) {
    .Card--Danger {
      width: 90%; } }

.Card--Success {
  background: rgba(23, 117, 63, 0.8); }
  @media screen and (max-width: 550px) {
    .Card--Success {
      width: 90%; } }

.RecipeCard {
  width: 300px;
  text-align: center;
  margin: 10px 0; }
  .RecipeCard__image {
    width: 300px;
    height: 175px;
    border-radius: 5px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.25); }
  .RecipeCard__title {
    color: #ffffff;
    margin: 5px 0; }
  .RecipeCard__button {
    border: 1px solid #ffffff;
    border-radius: 50px;
    font-size: 18px;
    padding: 10px 75px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    width: 100%; }
    .RecipeCard__button:hover {
      background: #F2C94C;
      border: 1px solid #F2C94C;
      color: #ffffff; }

.Spinner {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }

.Recipes {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 20px;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 50px 0; }
  @media screen and (max-width: 1366px) {
    .Recipes {
      display: grid;
      grid-template-columns: repeat(3, auto);
      grid-gap: 20px;
      -webkit-justify-content: center;
              justify-content: center; } }
  @media screen and (max-width: 1000px) {
    .Recipes {
      display: grid;
      grid-template-columns: repeat(2, auto);
      grid-gap: 20px;
      -webkit-justify-content: center;
              justify-content: center; } }
  @media screen and (max-width: 700px) {
    .Recipes {
      display: grid;
      grid-template-columns: repeat(1, auto);
      grid-gap: 20px;
      -webkit-justify-content: center;
              justify-content: center; } }

.Recipe {
  background: rgba(0, 0, 0, 0.5);
  width: 850px;
  display: -webkit-flex;
  display: flex;
  padding: 20px;
  margin: 20px auto;
  border-radius: 5px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.25); }
  @media screen and (max-width: 900px) {
    .Recipe {
      width: 90%;
      display: block; } }
  .Recipe__media {
    width: 40%; }
    @media screen and (max-width: 900px) {
      .Recipe__media {
        width: 100%;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between; } }
    @media screen and (max-width: 550px) {
      .Recipe__media {
        width: 100%;
        display: block; } }
    .Recipe__media img {
      width: 100%;
      height: 250px;
      margin-bottom: 20px;
      border-radius: 5px;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.25); }
      @media screen and (max-width: 900px) {
        .Recipe__media img {
          width: 48%; } }
      @media screen and (max-width: 550px) {
        .Recipe__media img {
          width: 100%;
          display: block; } }
    .Recipe__media iframe {
      width: 100%;
      height: 250px; }
      @media screen and (max-width: 900px) {
        .Recipe__media iframe {
          width: 48%; } }
      @media screen and (max-width: 550px) {
        .Recipe__media iframe {
          width: 100%;
          display: block; } }
  .Recipe__ingredients {
    padding: 0 20px;
    color: #ffffff; }
    @media screen and (max-width: 900px) {
      .Recipe__ingredients {
        padding: 10px 0;
        color: #ffffff; } }
  .Recipe__title {
    margin-bottom: 20px; }

